body {
  background: url("./images/S9l3Wg.jpg");
  background-size: cover;
  font-family: 
      'Times New Roman', 'Times', 'serif';
    font-weight: 1000;  
}

root {
  font-size: 10px;
}

#drum-machine {
    display: grid;    
    padding: 0;      
    width: 40rem;
    height: 20rem;    
} 

  #display {    
    text-align: center;                
    background: radial-gradient(
      circle,
     silver 20%,
      darkblue 65%,     
    );
    width: 100%;
    height: 100%;
    margin-top: 5%;
  }

  #soundButton {    
    width: 20rem;
    height: 20rem;
    max-width: 8rem;
    margin: 1px;    
  }

#audio {
    background-color: gray;    
    border: solid 1px silver;
    margin: 3px;

}

#soundinfo {
    display: flex;
    justify-content: space-between;
    flex-direction: row;    
    height: 5rem;
    align-items: center;        
}

#notes {   
   border: solid 3px darkgray;
   background-color: gray;
   color: #005493; 
   height: 2rem;
   width: 10rem;   
   margin-right: 1rem;
}

#ChangingSounds button {
    background-color: lightgray;
    color: lightblue;
    margin: 50px;
    font-weight: 900;
}
